<template>
    <div class="menu">
        <Card :bordered="false">
            <p slot="title">添加菜单</p>
            <Input type="text" v-model="New_menu.name" placeholder="菜单项名称"/>
            <Input type="text" v-model="New_menu.url" placeholder="菜单项链接"/>
            <Button @click="New_menu_post">提交</Button>
        </Card>
        <Table :columns="table.columns" :data="table.data"></Table>
    </div>
</template>
<script>
  export default{
    data () {
      return {
        value1: '1',
        New_menu:{
          name:"",
          url:""
        },
        table:{
          columns:[
            {
              title: '名称',
              key: 'Name'
            },
            {
              title: '链接',
              key: 'Url'
            },
            {
              title:"操作",
              key:"Option",
              render: (h, params) => {
                return h('div', [
                  h('Button', {
                    props: {
                      type: 'primary',
                      size: 'small'
                    }
                  }, '修改'),
                  h('Button', {
                    props: {
                      type: 'error',
                      size: 'small'
                    }
                  }, '删除')
                ]);
              }
            }
          ],
          data:[]
        }
      }
    },
    methods:{
      New_menu_post:function(){
        if(this.New_menu.name!==""&&this.New_menu.url!==""){
          this.$http.post("/console/menu/add",{name:this.New_menu.name,url:this.New_menu.url}).then(function(data){
            this.$Modal.success({
              title: "提示",
              content: "增加成功"
            });
            window.location.href="#";
          },function(){
            this.$Modal.error({
              title: "警告",
              content: "服务器发生错误"
            });
          })
        }else{
          this.$Modal.error({
            title: "警告",
            content: "您还未输入需要增加的内容"
          });
        }

      }
    },
    created () {
      this.$http.get("/console/menu/get").then(function (data) {
        data=data.body;
        let n=0;
        for(let i in data){
          this.$set(this.table.data,n,{
            Name:data[i]["name"],
            Url:data[i]["url"],
          })
          n++;
        }
      },function(error){
        alert("请求失败");
      });
    }
  }
</script>
